.focus {
  transition: transform 0.3s;
}

.focus:hover {
  transform: scale(1.05);
}

.card-title {
  font-size: 1em;
  font-weight: 300;
}

.title {
  font-size: 150%;
  font-weight: 700;
}
.image-style {
  display: block;
  margin: auto;
  width: 3em;
  transition: transform 0.3s;
}

.image-style:hover {
  transform: scale(2.05);
}

.font-details {
  font-size: 2em;
  font-weight: 500;
  color: #231d5af8;
}

.font-details-b {
  font-size: 3em;
  font-weight: 500;
  color: #ffffff;
}

.skill {
  cursor: pointer;
}
